import { Pipe, PipeTransform } from '@angular/core';
import { Recipe } from "../models/recipe";

@Pipe({name: 'filterRecipes'})
export class FilterRecipesPipe implements PipeTransform {
    transform(value: Recipe[], category: string): Recipe[] {
        var tmpRecipes = [];
        for(let recipe of value){
            if(recipe.categories.indexOf(category) > -1){
                tmpRecipes.push(recipe);
            }
        }
        return tmpRecipes;
    }
}
